import React from 'react';
import Form from 'react-bootstrap/Form';

function Apply() {
  const mail = "que.org.members@gmail.com"

  const translation = {
   'ja' :{
     titles: {
      name: "お名前",
      mail: "メールアドレス",
      affiliation: "ご所属",
      session2: "セッション2の参加",
      content: "備考"
     },
     session2 : [
       "希望する",
       "希望しない"
     ],
     placeholder: {
       name: "佐藤 太郎"
     },
     required: {
       true: "必須",
       false: "任意"
     },
     send: "送信"
    }
  }

  const {titles, session2, placeholder, required, send} = translation['ja']

  const isRequired = (input) => {
    return (
      <div className={input ? "required" : "optional"}>
        {input ? required.true : required.false}
      </div>
    )
  }
  

  return (
    <div className="contact-form">
      <form method="POST" action={"https://formspree.io/" + mail} >
        <h5 className="sec-title">{titles.name} <br/>{isRequired(true)}</h5>
        <input type='text' name='お名前' placeholder={placeholder.name} />
        <h5 className="sec-title">{titles.mail} <br/>{isRequired(true)}</h5>
        <input type="email" name="返信先" placeholder="you@example.com"/>
        <h5 className="sec-title">{titles.affiliation}<br/>{isRequired(false)}</h5>
        <input type='text' name='所属'  />
        <h5 className="sec-title">{titles.session2} <br/>{isRequired(true)}</h5>
        <Form.Control name='セッション２への参加' as="select">
          {
            session2.map((t) => (<option key={t} value={t}>{t}</option>))
          }
        </Form.Control>
        <h5 className="sec-title">{titles.content} <br/>{isRequired(true)}</h5>
        <textarea name='備考'></textarea>
        <input className='submit' type="submit" value={send}/>
      </form>
    </div>
  );
}

export default Apply;